.App {
  text-align: center;
}

body {
  width: 100%;
  height: 100%;
  background-image: url(./pokemon_wallpaper.jpg);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

li {
  list-style: none;
}

.pinned-content {
  padding-top: 15px;
  left: 0;
  right: 0;
  position: fixed;
  background-color: #333333;
  color: white;
  z-index: 99 !important;
}

h1 { 
  font-family: 'Fredoka One', cursive;
  cursor: pointer;
  font-size: 32px;
}
.pinned-content h5, .pinned-content h1 {
  padding: 10px;
}
.pinned-content h5 {
  padding-bottom: 0px;
  font-size: 18px;
}

.pinned-content p {
  font-size: 14.5px;
  margin-bottom: 12px;
}

.pokemon-list {
  margin: 0 auto;
  padding-left: 8vw;
  padding-right: 9vw;
  padding-top: 190px;
  min-height: 101vh;
}

.pokemon {
  padding: 5px;
  padding-top: 6px;
  padding-left: 18px;
  padding-right: 18px;
  background-color: #333333;
  opacity: 0.95;
  color: white;
  width: 220px;
  height: 256px;
  margin: 15px;
  border-radius: 20px;
}

.pokemon-list li {
  display: inline-block;
  vertical-align: top;
}

.pokemon img {
  width: 110px;
  height: 110px;
}

img.type {
  width: initial;
  height: initial;
  margin: 2px;
}

.dropdown {
  display: inline;
  margin: 10px !important;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.pokemon span {
  font-size: 14px;
}

#pokenumber {
  text-align: left;
}

.inline-heading {
  position: relative;
  line-height: 1.5em;
}

.inline-heading span {
  /* display: inline; */
  float: left;
  font-size: 12px;
}

.pokemon h3 {
  font-size: 24px;
  padding-right: 12px;
}

.btn-sm {
  min-width: 145px !important;
}

.heart {
  color: transparent;
  text-shadow: 0px 0px 1px white;
  font-size: 18px;
  float: right;
  margin-left: 72px;
  margin-top: 18px;
  position: absolute;
  z-index: 2;
  cursor: pointer;
  background-color: transparent;
  border-color: transparent;
  text-align: center;
}

.pokemon-li  {
  position: relative;
  cursor: pointer;
}

#ascendingToggle {
  min-width: 95px !important;
  margin-left: -6px;
  margin-right: 40px;
}

input {
  height: 30px;
  padding-bottom: 5px;
}

#clearCache {
  position: absolute;
  margin-left: 30vw;
}

#favoritestoggle, #shinytoggle {
  max-height: 28px;
  line-height: 14px;
  margin-bottom: 2px;
}

.unselectable {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}